import React, { useState } from "react";

export default function Calculator() {
  let [value, setValue] = useState('');

  const MakeExp = (event) => {
        const btn = event.target.innerText;
        if(btn === 'C') {
            setValue('')
        }
        else {
            setValue((pvalue)=>pvalue+btn);
        }
    }

    const EvaluateExp = () => {
        try {
            setValue(eval(value));
        } catch(e) {
            setValue('')
            console.log(e)
        }
    }

  return (
    <div className="container w-50">
      <div className="display">
        <input className="form-control" type="text" readOnly value={value} />
      </div>
      <div className="btns">
        <div className="row">
          <div className="col-3 my-2">
            <button className="btn btn-secondary operator" onClick={MakeExp}>-</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary operator" onClick={MakeExp}>+</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary operator" onClick={MakeExp}>*</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary operator" onClick={MakeExp}>/</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary" onClick={MakeExp}>7</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary" onClick={MakeExp}>8</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary" onClick={MakeExp}>9</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary operator" onClick={MakeExp}>.</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary" onClick={MakeExp}>4</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary" onClick={MakeExp}>5</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary" onClick={MakeExp}>6</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary operator" onClick={MakeExp}>C</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary" onClick={MakeExp}>1</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary" onClick={MakeExp}>2</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary" onClick={MakeExp}>3</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary" onClick={EvaluateExp}>=</button>
          </div>
          <div className="col-3 my-2">
            <button className="btn btn-secondary" onClick={MakeExp}>0</button>
          </div>
        </div>
      </div>
    </div>
  );
}
