import React from "react";
import { Link } from "react-router-dom";
import "./css/Login.css"

export default function Navbar(props) {
  return (
    <nav className="navbar navbar-expand-lg bg-body-dark">
      <div className="container-fluid">
        <Link className="navbar-brand" to="/">
          {props.title}
        </Link>
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div className="collapse navbar-collapse" id="navbarSupportedContent">
          <ul className="navbar-nav me-auto">
            <li className="nav-item">
              <Link className='nav-link' style={{color : `${props.theme === 'Light' ? 'white' : 'black'}`}} aria-current="page" to="/calculator">
                Calculator
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link" aria-current="page" to="/" style={{color : `${props.theme === 'Light' ? 'white' : 'black'}`}} >
                TextConverter
              </Link>
            </li>
            <li className="nav-item">
              <Link className="nav-link light" aria-current="page" to="/todolist" style={{color : `${props.theme === 'Light' ? 'white' : 'black'}`}} >
                TodoList
              </Link>
            </li>
          </ul>
          <div className="form-check form-switch">
            <input
              className="form-check-input"
              onClick={props.ChangeTheme}
              type="checkbox"
              role="switch"
              id="flexSwitchCheckDefault"
            />
            <label
              className="form-check-label"
              htmlFor="flexSwitchCheckDefault"
            >
              {props.theme} Mode
            </label>
          </div>
        </div>
      </div>
    </nav>
  );
}
