import React , {useState} from "react";

export default function TextConvert() {
  const [text, setText] = useState("Enter text");

  const handleText = (event) => {
    const newText = text.toUpperCase();
    setText(newText);
  };

  const handleTextPlay = () => {
    let msg = new SpeechSynthesisUtterance();
    msg.text = text;
    window.speechSynthesis.speak(msg);
  };

  const handleChangeText = (event) => {
    setText(event.target.value);
  };

  return (
    <div className="container">
      <textarea
        type="text"
        className="form-control my-4"
        rows={3}
        value={text}
        onChange={handleChangeText}
      ></textarea>
      <button className="btn btn-secondary mx-2" onClick={handleText}>
        Convert
      </button>
      <button className="btn btn-secondary mx-2" onClick={handleTextPlay}>
        Play
      </button>
      <h4>{text.length} characters</h4>
    </div>
  );
}
